.profile-header {
    position: relative;
    overflow: hidden;
}

.profile-header .profile-header-cover {
    background-color: #4e5d6c;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
}

.profile-header .profile-header-cover:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(255, 255, 255, 0.25) 100%);
}

.profile-header .profile-header-content {
    color: #fff;
    padding: 50px 40px;
    @include responsive(1000px) {
        padding: 30px 20px;
    }
    @include responsive(320px) {
        padding: 20px 10px;
    }
}

.profile-header .profile-header-content.carousel-view {
    flex-direction: column;
    align-items: center;
    justify-content: center;

    .profile-header {
        &-info {
            margin-right: 0;
            text-align: center;
        }
        &-img {
            margin-right: 0;
            margin-bottom: 1em;
        }
    }
}

.profile-header-img {
    padding: 3px;
    border-radius: 50%;
    background: #fff;
}

.profile-header-info h4 {
    font-weight: 500;
    color: #fff;
}

.profile-header-info {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

