.profile-header .profile-header-tab {
    background: #4e5d6c;
    list-style-type: none;
    padding: 0 0 0 140px;
    white-space: nowrap;
    border-radius: 0;
    display: flex;
    justify-content: center;
    padding: 0;
}

.text-ellipsis,
.text-nowrap {
    white-space: nowrap !important;
}

.profile-header .profile-header-tab > li {
    display: inline-block;
    margin: 0;
}

.profile-header .profile-header-tab > li > a {
    display: block;
    color: #929ba1;
    line-height: 20px;
    padding: 10px 20px;
    text-decoration: none;
    font-weight: 700;
    font-size: 12px;
    border: none;
}

