.timeline {
    list-style-type: none;
    margin: 0;
    padding: 0;
    position: relative;
}

.timeline > li {
    position: relative;
    min-height: 50px;
}

.timeline .timeline-time {
    position: absolute;
    left: 0;
    width: 18%;
    text-align: right;
    top: 30px;
}

.timeline .timeline-time .date,
.timeline .timeline-time .time {
    display: block;
    font-weight: 600;
}

.timeline .timeline-time .date {
    line-height: 16px;
    font-size: 12px;
}

.timeline .timeline-time .time {
    line-height: 24px;
    font-size: 20px;
    color: #242a30;
}

.timeline .timeline-icon {
    left: 15%;
    position: absolute;
    width: 10%;
    text-align: center;
    top: 40px;
}

.timeline .timeline-icon a {
    text-decoration: none;
    width: 20px;
    height: 20px;
    display: inline-block;
    border-radius: 20px;
    background: #d9e0e7;
    line-height: 10px;
    color: #4e5d6c;
    font-size: 14px;
    border: 5px solid #2d353c;
    transition: border-color 0.2s linear;
}

.timeline .timeline-body {
    margin-left: 15%;
    margin-right: 15%;
    background: #4e5d6c;
    position: relative;
    padding: 20px 0;
    border-radius: 6px;
    margin-bottom: 30px;

    @include responsive(1000px) {
        margin-left: 0;
        margin-right: 0;
        margin-bottom: 20px;
    }
}

.timeline .timeline-body > div + div {
    margin-top: 15px;
}

.timeline .timeline-body > div + div:last-child {
    margin-bottom: -20px;
    padding-bottom: 20px;
    border-radius: 0 0 6px 6px;
}

.timeline-header {
    border-bottom: 1px solid rgb(163, 163, 163);
    margin-left: 25px;
    margin-right: 25px;
    display: flex;
    // align-items: center;
    justify-content: space-between;
    padding-bottom: 1em;
}

.post-user-details {
