.grid-timeline {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 1em;
    row-gap: 1em;
    padding-bottom: 1em;
    .post-item {
        .timeline-body {
            margin: 0;
            padding: 0;
            border-radius: 0;
            height: 100%;
            .timeline-header,
            .timeline-footer,
            .stats,
            .stats-right,
            .timeline-content__body {
                display: none;
            }
            .timeline-content,
            .timeline-likes {
                margin: 0;
                padding: 0;
